import axios from "../config/axios";

axios.defaults.withCredentials = true;
export default {
    register: (data) => {

    },

    login: (data) => {
        return csrf().then(response => {
            return axios.post('/login', {...data, remember: 'on'})
        })

    },

    me: () => {
        return csrf().then(response => {
            return axios.get('/api/user')
        })
    },

}

function csrf() {
    axios.defaults.withCredentials = true;
    return axios.get('/sanctum/csrf-cookie')
}