import React from 'react'
import {Layout, Menu, Result, Button } from 'antd';
import {Route, useHistory, Switch} from 'react-router-dom';
import Orders from "../Orders";
import Clients from "../Clients";
import Client from "../Clients/Client";
import ClientForm from "../Clients/component/ClientForm";

import Foods from "../Foods";
import Food from "../Foods/Food";
import FoodForm from "../Foods/component/FoodForm";


import {
    PieChartOutlined,
    UserOutlined,
    ShopOutlined
} from '@ant-design/icons'
import {BottomNav} from "../../components";
import ShowOrder from "../Orders/ShowOrder";
import OrderForm from "../Orders/OrderForm";

const {Content, Footer, Sider} = Layout;

const defaultSelected = () => {
    if (window.location.pathname === '/') {
        return "1"
    } else if (window.location.pathname.includes('clients')) {
        return "2";
    } else if (window.location.pathname.includes('foods')) {
        return "3";
    }
}

const HomePage = (props) => {
    const [collapsed, setCollaps] = React.useState(false);
    const history = useHistory();

    return (
        <Layout className="layout" style={{minHeight: '100vh'}}>

            <Sider className={"sider"} collapsible collapsed={collapsed} onCollapse={() => setCollaps(!collapsed)}>
                <div className="logo"/>
                <Menu theme="dark" defaultSelectedKeys={[defaultSelected()]} mode="inline">
                    <Menu.Item key="1" onClick={() => history.push('/')}>
                        <PieChartOutlined/>
                        <span>Заказы</span>
                    </Menu.Item>
                    <Menu.Item key="2" onClick={() => history.push('/clients')}>
                        <UserOutlined/>
                        <span>Клиенты</span>
                    </Menu.Item>

                    <Menu.Item key="3" onClick={() => history.push('/foods')}>
                        <ShopOutlined/>
                        <span>Ассортимент</span>
                    </Menu.Item>

                </Menu>
            </Sider>


            <Layout>
                <Content style={{padding: '0 50px'}}>

                    <Switch>
                        <Route exact path={'/'} component={Orders}/>
                        <Route exact path={'/orders/create'} component={OrderForm}/>
                        <Route exact path={'/orders/:id(\\d+)'} component={ShowOrder}/>

                        <Route exact path={'/clients'} component={Clients}/>
                        <Route exact path={'/clients/create'} component={ClientForm}/>
                        <Route exact path={"/clients/:id(\\d+)"} component={Client}/>
                        <Route exact path={'/clients/:id/edit'} component={ClientForm}/>
                        <Route exact path={'/clients/:id/edit'} component={ClientForm}/>

                        <Route exact path={'/foods'} component={Foods}/>
                        <Route exact path={'/foods/create'} component={FoodForm}/>
                        <Route exact path={"/foods/:id(\\d+)"} component={Food}/>
                        <Route exact path={'/foods/:id/edit'} component={FoodForm}/>

                        <Route component={() => {
                            return (
                                <Result
                                    status="404"
                                    title="404"
                                    subTitle="Sorry, the page you visited does not exist."
                                    extra={<Button onClick={() => history.push('/')} type="primary">Вернуться на главную</Button>}
                                />
                            )
                        }} />
                    </Switch>


                </Content>

                {(/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)) && <BottomNav/> }

                <Footer style={{textAlign: 'center'}}>Ant Design ©2018 Created by Ant UED</Footer>
            </Layout>
        </Layout>
    )
}

export default HomePage;