import React from "react";
import './CreateOrder.scss'
import {Breadcrumb, Button, Result} from "antd";
import {useHistory} from 'react-router-dom'
import _form from './_form';
import {FormProvider, FormContext} from "./FormProvider";

const OrderForm = props => {
    return (
        <FormProvider>
            <div className={"site-content"}>

                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Заказы</Breadcrumb.Item>
                </Breadcrumb>

                <div className={"site-layout-content"}>
                    <Container/>
                </div>

            </div>
        </FormProvider>
    )
}


const Send = () => {
    const context = React.useContext(FormContext);

    React.useEffect(_ => {
    }, [])

    return (
        <Button onClick={context.formik.handleSubmit} type={"primary"} style={{marginTop: 5}}>Добавить
            ({context.sum}Р)</Button>
    )

}


const Success = ({order}) => {
    const history = useHistory();

    return (
        <Result
            status="success"
            title="Заказ успешно добавлен"
            subTitle={`Заказ успешно добавлен под номером: ${order.id}`}
            extra={[
                <Button onClick={() => history.push(`/orders/${order.id}`)} type="primary" key="console">
                    Перейти к заказу
                </Button>,
            ]}
        />
    )
}

const Container = () => {
    const {order} = React.useContext(FormContext);

    React.useEffect(_ => {
    }, [])

    return (
        null === order ? <React.Fragment>
                <_form/>

                <Send/>
            </React.Fragment>
            : <Success order={order}/>
    )
}

export default OrderForm