import React from "react";
import {
    PieChartOutlined,
    UserOutlined,
    ShopOutlined
} from '@ant-design/icons'

import './BottomNav.scss';
import {Link} from "react-router-dom";
export default () => {
    return (
        <div className="navbar">
            <Link to="/" className={window.location.pathname === '/' ? 'active' : ''}>
                <PieChartOutlined/>
                <span> Заказы</span>
            </Link>
            <Link to="/clients" className={window.location.pathname.includes('clients')  ? 'active' : ''}>
                <UserOutlined/>
                <span> Клиенты</span>
            </Link>

            <Link to="/foods" className={window.location.pathname.includes('foods')  ? 'active' : ''}>
                <ShopOutlined/>
                <span> Ассортимент</span>
            </Link>

        </div>
    )
}