import React from 'react'
import {orderApi} from "../../../core/api";
import {Breadcrumb, Descriptions, Badge, List, Typography, Divider} from 'antd';
import {LetterOrAvatar} from "../../../components";


import './OrderShow.scss'
import {Link} from "react-router-dom";

const {Title} = Typography;


const statuses = {
    0: {
        text: 'Не выполнено',
        color: 'red'
    },
    1: {
        text: 'Выполнено',
        color: 'green'
    }
}


const ShowOrder = props => {
    const [order, setOrder] = React.useState(null);

    React.useEffect(() => {
        orderApi.show(props.match.params.id)
            .then(response => {
                if (null === response.data.data) {
                    props.history.push('/404')
                }
                setOrder(response.data.data);
            })
            .catch(err => {
                props.history.push('/404')
            })
    }, [])

    if (order === null) {
        return <div>

        </div>
    }

    return (
        <div className={'site-content'}>

            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/orders"}>
                        Заказы
                    </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>{order?.name}</Breadcrumb.Item>
            </Breadcrumb>

            <div className={"site-layout-content"}>
                <Descriptions title="Общая информация">
                    <Descriptions.Item label="Имя">{order.name}</Descriptions.Item>
                    <Descriptions.Item label="Номер телефона">{order.phone}</Descriptions.Item>
                    <Descriptions.Item label="Адрес">{order.address || 'Не заполнено'}</Descriptions.Item>
                    <Descriptions.Item label="Сумма заказа">
                        <Badge style={{backgroundColor: '#52c41a'}} count={order.total_sum + 'Р'}
                               overflowCount={1000000}/>
                    </Descriptions.Item>
                    <Descriptions.Item label="Дата доставки">
                        {order.date_delivery}
                    </Descriptions.Item>

                    <Descriptions.Item label="Статус">
                        <Badge color={statuses[order.status].color} text={statuses[order.status].text}/>
                    </Descriptions.Item>

                </Descriptions>

                <Divider/>

                <div>

                    <Title level={4}>Заказ</Title>

                    <List
                        itemLayout="horizontal"
                        dataSource={order.foods}
                        renderItem={item => (
                            <List.Item>
                                <List.Item.Meta
                                    avatar={<LetterOrAvatar url={item.image} letter={item.letter} name={item.name}/>}
                                    title={item.name}
                                    description={<div className={"order"}>
                                        <span>{item.pivot.comment}</span>
                                        <Badge style={{backgroundColor: '#52c41a'}}
                                               count={item.pivot.count + ' ' + item.pivot.unit}
                                               overflowCount={1000000}/>
                                    </div>}
                                />
                            </List.Item>
                        )}
                    />
                </div>

            </div>

        </div>
    )
}

export default ShowOrder